
import Box from 'vux/src/components/box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
name: 'RefreshData',
directives: {
TransferDom
},
components: {
Box,
Confirm,
Countup,
Divider,
Marquee,
MarqueeItem,
TransferDom,
XButton,
XCircle,
XDialog
},
data () {
return {
lastRefreshTime: '2018-11-25 20:00',
percent: 0,
marqueeMsg0: [
'Tip: 每 40 分钟才可同步一次哦～',
'动动手指，获取最新跑操数据吧',
'Tip: 只有连接 seu-wlan 才可进行同步操作',
'我为人人，人人为我～'
],
marqueeMsg1: [
'稍等片刻，马上就好',
'Tip: 每 40 分钟才可同步一次哦～',
'秋意正好，不如喝杯热水休息一下',
'Tip: 只有连接 seu-wlan 才可进行同步操作',
'微微正在努力同步中～',
'Tip: 每次同步耗时约为一分钟哦',
'关注小微 GitHub，一起参与开发～',
'Tip: 如有问题，欢迎致信微微邮箱'
],
marqueeMsg: null,
step: 0,
show2: true,
showHideOnBlur: true
}
},
created: function () {
this.marqueeMsg = this.marqueeMsg0
},
methods: {
stepPlus () {
this.step += 1
this.percentPlus()
this.marqueeMsg = this.marqueeMsg1
if (this.step === 3) {
this.percent = 200
this.marqueeMsg = this.marqueeMsg0
}
},
percentPlus () {
this.percent += (100 / 2)
}
}
}



